import header from '@/components/header';
import LingLoading from '@/components/attendance/LingLoading';
export default {
  data() {
    return {
      titleContent: '与园长进行沟通',
      mails: [],
      firstMail: [],//第一条消息
      userId: '',
      userInfo: [],
      sendContent: '',
      mailId: '',
      disabledSend: false,
      defaultAvatar: 'http://cdn-didano.oss-cn-shenzhen.aliyuncs.com/xiaonuo/wx/img/logo_2.jpg',
      state: {
        isShow: true,
        message: '加载中',
        state: 'loading'
      },
    }
  },
  created() {
    //获取localStorage
    this.userInfo = JSON.parse(localStorage.getItem('CURRENT_USER'));
    if (this.userInfo.roleType === PARENT) {
      this.userId = this.userInfo.parentId;
    } else {
      this.userId = this.userInfo.staffId;
      this.titleContent = this.$route.query.dialogueTitle;
    }
    // 根据maiId获取内容列表
    if (this.$route.query.mailId || this.mailId != 0) {
      this.mailId = this.$route.query.mailId;
      this._initMailContent();
    } else if (this.mailId == 0) {
      this.getMailIdByUserId();
    }

  },
  methods: {
    // 获取消息内容
    _initMailContent() {
      this.$http.post(`${WX_URL}base/mailbox/post/findReply_ByNoticeId/${this.mailId}`).then((response) => {
        response = response.data;
        if ( response.success ) {
          this.state.isShow = false;
          this.mails = response.data;
          this.firstMail = this.mails.mai;
        }
      },(response) => {
        this.state.state = 'error';
        this.state.message = '网络错误';
      });
    },
    //发送消息，发送消息成功获取消息的ID
    sendMessage() {
      if (this.sendContent) {
        this.disabledSend = true;
      }
      if (this.disabledSend) {
        this.$http.post(`${WX_URL}base/mailbox/post/write_mail`,
          {
            'content':this.sendContent,
            'mailId':this.mailId,
            'studentId':this.userInfo.studentId,
            'userId':this.userId,
            'userType':this.userInfo.roleType}
          ).then( (response) => {
            response = response.data;
            if (response.success) {
              this.mailId = response.data;
              this.sendContent = '';
              this._initMailContent();
            }
          });
      }
    },
    //删除发送消息
    deleteMail(mail) {
      this.$http.post(`${WX_URL}base/mailbox/post/deleteMailByBoss/${mail.id}`).then( (response) => {
        response = response.data;
        if( response.success ) {
          this._initMailContent();
        }
      })
    },
    // 根据用户的ID获取mailId(家长身份从园长信箱入口)
    getMailIdByUserId() {
      this.$http.post(`${WX_URL}base/mailbox/post/parentGetMailId/${this.userId}`).then((response)=> {
        response = response.data;
        if (response.success) {
          this.mailId = response.data;
          if (this.mailId) {
            this._initMailContent();
          } else {
            this.state.isShow = false;
          }
        }
      });
    },
    fetchData() {
      this._initMailContent();
    }
  },
  components: {
    'v-header': header,
    LingLoading
  }
}



